/* 1. Main Layout ================================= */

* {
	box-sizing: border-box;
}

body {
	background-color: #f8fcff;
	background-image: url("../img/background-pattern.png");
	height: 3000px;
}


.content {
	padding: 30px;
	font-family: sans-serif;
	margin: 30px auto;
	border-radius: 10px;
}



/* 2. Content ================================= */

header{
	width: 860px;
	margin: 30px auto;
	padding: 30px;
	
}

.logo {

	h1{
		a{
			text-decoration: none;
			background: url(../img/logo-copy.svg);
			text-indent: -999999px;
			overflow: hidden;
			width: 216px;
			height: 40px;

		}

	}
}

.title{
	height: 67px;
	width: 590px;
	margin: 30px auto;
}

nav {
	text-align: center;
	ul{
		li{
			display: inline-block;
			text-transform: uppercase;
			
			a{
				text-decoration: none;
				color: #4a5666;
				font-family: helvetica, arial, sans-serif;
			}
		}
	}
}

.custom{
	background-image: url("../img/custom-background.png");
	width: 860px;
	height: 520px;

		h2{
			color: #4a5666;
			text-transform: uppercase;

		}
}

.crafting{
	background-image: url("../img/product-info-background.png");
	width: 860px;
	height: 520px;
}

.mission{
	background-image: url("../img/mission-background.png");
	width: 860px;
	height: 1442px;
}

